body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #1a1b15;
  color: #FFFFFF;
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}
@media (min-width: 768px) {
  body {
    height: 100%;
  }
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #FFFFFF;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #FFFFFF;
  font-size: 2rem;
}

.nombre-pagina {
  text-align: center;
  margin-bottom: 5rem;
  margin-top: 2rem;
  margin-right: 0rem;
  margin-left: 0rem;
  font-weight: 700;
}

.descripcion-pagina {
  text-align: center;
}

@media (min-width: 768px) {
  .contenedor-app {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 1;
    min-height: 100vh;
  }
}
.contenedor-app .imagen {
  height: 20rem;
  background-image: url(../img/1.jpg);
  background-size: cover;
  background-position: center center;
  border-radius: 10px;
}
@media (min-width: 768px) {
  .contenedor-app .imagen {
    height: auto;
  }
}
.contenedor-app .app {
  width: 95%;
  margin: 0 auto;
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .contenedor-app .app {
    overflow: scroll;
    scrollbar-width: none; /* Para navegadores WebKit */
    overflow: -moz-scrollbars-none; /* Para navegadores Gecko */
  }
}

.barra {
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .barra {
    display: flex;
    justify-content: space-between;
    align-items: center; /* Alinea los elementos verticalmente en el centro */
  }
}
.barra .boton {
  background-color: rgba(137, 43, 226, 0.721);
  padding: 1rem 1.5rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 4rem 0;
  text-align: center;
}
@media (min-width: 768px) {
  .barra .boton {
    width: auto;
  }
}
.barra .boton:hover {
  background-color: rgba(111, 26, 192, 0.721);
  cursor: pointer;
}

.barra-servicios {
  margin-bottom: 2.5rem;
}
@media (min-width: 768px) {
  .barra-servicios {
    gap: 0.5rem;
    display: flex;
    justify-content: space-between;
  }
}
.barra-servicios .boton {
  margin: 0 0 1rem 0;
}

.barra-servicios-green {
  margin-bottom: 0.5rem;
  gap: 0.5rem;
}
@media (min-width: 768px) {
  .barra-servicios-green {
    display: flex;
    justify-content: flex-start;
  }
}
.barra-servicios-green .boton-green {
  margin-bottom: 1rem;
  width: 100%; /* Nuevo */
}
.barra-servicios-green .boton-red {
  width: 100%;
}
@media (min-width: 768px) {
  .barra-servicios-green .boton-red {
    width: 25vh;
  }
}
@media (min-width: 1220px) {
  .barra-servicios-green .boton-red {
    width: 50vh;
  }
}

.seccion {
  animation: slide-in-blurred-top 0.5s cubic-bezier(0.23, 1, 0.32, 1) both;
  padding: 4rem 0;
  display: none;
}
@-webkit-keyframes slide-in-blurred-top {
  0% {
    -webkit-transform: translateY(-1000px) scaleY(2.5) scaleX(0.2);
    transform: translateY(-1000px) scaleY(2.5) scaleX(0.2);
    -webkit-transform-origin: 50% 0%;
    transform-origin: 50% 0%;
    -webkit-filter: blur(40px);
    filter: blur(40px);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0) scaleY(1) scaleX(1);
    transform: translateY(0) scaleY(1) scaleX(1);
    -webkit-transform-origin: 50% 50%;
    transform-origin: 50% 50%;
    -webkit-filter: blur(0);
    filter: blur(0);
    opacity: 1;
  }
}
@keyframes slide-in-blurred-top {
  0% {
    -webkit-transform: translateY(-1000px) scaleY(2.5) scaleX(0.2);
    transform: translateY(-1000px) scaleY(2.5) scaleX(0.2);
    -webkit-transform-origin: 50% 0%;
    transform-origin: 50% 0%;
    -webkit-filter: blur(40px);
    filter: blur(40px);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateY(0) scaleY(1) scaleX(1);
    transform: translateY(0) scaleY(1) scaleX(1);
    -webkit-transform-origin: 50% 50%;
    transform-origin: 50% 50%;
    -webkit-filter: blur(0);
    filter: blur(0);
    opacity: 1;
  }
}
.seccion .text-center {
  text-align: center;
}

.mostrar {
  display: block;
}

.ocultar {
  visibility: hidden;
}

.listado-servicios {
  display: grid;
  gap: 2rem;
}
@media (min-width: 768px) {
  .listado-servicios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.listado-servicios .servicio {
  background-color: #FFFFFF;
  border-radius: 10px;
  text-align: center;
  padding: 1rem;
  transition: transform 0.2s ease-in-out;
  cursor: pointer;
}
@-webkit-keyframes shadow-inset-center {
  0% {
    -webkit-box-shadow: inset 0 0 0 0 rgba(0, 0, 0, 0);
    box-shadow: inset 0 0 0 0 rgba(0, 0, 0, 0);
  }
  100% {
    -webkit-box-shadow: inset 0 0 14px 0px rgba(0, 0, 0, 0.5);
    box-shadow: inset 0 0 14px 0px rgba(0, 0, 0, 0.5);
  }
}
@keyframes shadow-inset-center {
  0% {
    -webkit-box-shadow: inset 0 0 0 0 rgba(0, 0, 0, 0);
    box-shadow: inset 0 0 0 0 rgba(0, 0, 0, 0);
  }
  100% {
    -webkit-box-shadow: inset 0 0 14px 0px rgba(0, 0, 0, 0.5);
    box-shadow: inset 0 0 14px 0px rgba(0, 0, 0, 0.5);
  }
}
.listado-servicios .servicio.seleccionado {
  background-color: #0da6f3;
  animation: shadow-inset-center 2s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}
.listado-servicios .servicio.seleccionado:hover {
  background-color: #0a85c3;
}
.listado-servicios .servicio.seleccionado p:not(.precio-servicio) {
  color: #FFFFF3;
}
.listado-servicios .servicio.seleccionado .precio-servicio {
  color: gold;
}
.listado-servicios .servicio:hover {
  transform: scale(0.97);
  background-color: azure;
}
.listado-servicios .servicio p {
  font-weight: 600;
  color: #1a1b15;
  margin: 0 0 1rem 0;
}
.listado-servicios .servicio .precio-servicio {
  color: #0da6f3;
  font-weight: bold;
  margin: 0;
}

.contenido-resumen p {
  margin-bottom: 1rem;
  color: #FFFFF3;
}
.contenido-resumen span {
  color: #0da6f3;
  font-weight: bold;
  font-size: 2.4rem;
}

.contenedor-servicio {
  padding: 3rem 0;
  border-bottom: 1px solid gold;
}
.contenedor-servicio:last-of-type {
  margin-bottom: 5rem;
}

.citas {
  list-style: none;
  padding: 0;
}
.citas li {
  padding: 1rem;
  border-top: 1px solid #FFFFFF;
}
.citas li p {
  font-weight: 700;
  color: #0da6f3;
}
.citas li span, .citas li .servicio {
  font-weight: 400;
  color: #FFFFFF;
}
.citas li:hover {
  transform: scale(1.02);
  transition: transform 0.3s ease, color 1.5s ease-in-out; /* Agrega una transición suave */
}
.citas li:hover span, .citas li:hover .servicio {
  color: #f7f7d8;
}
.citas li:not(:hover) {
  transform: scale(1);
  transition: transform 0.3s ease;
}

.servicios {
  list-style: none;
  padding: 0;
}
.servicios li {
  padding: 1rem;
  border-top: 1px solid #FFFFFF;
}
.servicios li p {
  font-weight: 700;
  color: #0da6f3;
}
.servicios li span, .servicios li .servicio {
  font-weight: 400;
  color: #FFFFFF;
}
.servicios li:hover {
  transition: border 1.5s ease-in-out; /* Agrega una transición suave */
}
.servicios li:hover span, .servicios li:hover .servicio {
  color: rgb(231, 211, 93);
}

.formulario {
  width: 100%;
