.campo input:not([type=submit]), .campo textarea, .campo select {
  padding: 1.4rem;
  display: block;
  justify-content: center;
  align-items: center;
  border: 1px solid #e1e1e1;
  border-radius: 8px;
  transition: transform 0.2s ease-in-out;
  background-color: #6d7158;
  color: #FFFFFF;
}
.campo input:not([type=submit]):disabled, .campo textarea:disabled, .campo select:disabled {
  cursor: not-allowed;
}
.campo input:not([type=submit])::placeholder, .campo textarea::placeholder, .campo select::placeholder {
  color: #FFFFFF;
}
.campo input:not([type=submit]):hover, .campo textarea:hover, .campo select:hover {
  transform: scale(0.985);
  color: gold;
}

.boton {
  background-color: #0da6f3;
