.boton.more-width {
  transition: transform 0.2s ease-in-out, color 0.2s ease-in-out, background-color 0.2s ease-in-out; /* Se agregan propiedades para color y background-color */
  width: 100%;
}
.boton.more-width:hover {
  transform: scale(0.98);
  cursor: pointer;
  color: gold;
  background-color: #0875ab;
}
.boton.more-width.actual {
  /* Se agregan las propiedades para el estado "actual" */
  transform: scale(1); /* Se anula la transformación del hover */
  pointer-events: none;
  background-image: linear-gradient(to right, #00ffff, #ff00c3);
  animation: bounce-top 0.9s forwards;
  color: #FFFFF3;
}

