.acciones a:hover {
  transform: scale(1.1);
  background: linear-gradient(90deg, #00ffff, #ff00c3);
  background-clip: text;
  -webkit-text-fill-color: transparent;
}
.acciones a:active {
  text-shadow: 0px 0px 10px gold, 0px 0px 20px gold, 0px 0px 40px gold, 0px 0px 80px gold;
}

.alerta {
  width: 50%;
  margin: 0 auto;
  margin-bottom: 1rem;
  color: rgb(255, 255, 255);
  font-weight: 600;
  font-size: 2rem;
  text-align: center;
}
@keyframes aparecer {
  0% {
    opacity: 0;
  } /* Opacidad al inicio de la animación */
  100% {
    opacity: 1;
  } /* Opacidad al final de la animación */
}
.alerta.errorlog {
  background-color: rgb(146, 5, 5);
  border-radius: 5px;
  animation: aparecer 2s forwards;
}
.alerta.correctlog {
  background-color: rgb(15, 162, 30);
  border-radius: 5px;
  animation: aparecer 2s forwards;
}
.alerta.ocultar-alerta {
  transform: translateX(180%);
  transition: transform 1.5s ease; /* Agrega una transición suave */
}

@media (min-width: 768px) {
  .tabs {
    display: flex;
    gap: 1rem;
  }
}
.tabs button {
  background-color: #0da6f3;
  border: none;
  color: #FFFFFF;
  padding: 1rem;
  display: block;
  width: 100%;
  margin-bottom: 1rem;
  border-radius: 10px;
  text-transform: capitalize;
  font-weight: bold;
  transition: transform 0.2s ease-in-out, background-color 0.2s ease-in-out, background-image 1.2s ease-in-out;
}
@keyframes gradient-animation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
.tabs button:hover {
  transform: scale(0.98);
  cursor: pointer;
  color: gold;
  background-color: #0875ab;
}
@-webkit-keyframes bounce-top {
  0% {
    -webkit-transform: translateY(-45px);
    transform: translateY(-45px);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
    opacity: 1;
  }
  24% {
    opacity: 1;
  }
  40% {
    -webkit-transform: translateY(-24px);
    transform: translateY(-24px);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
  }
  65% {
    -webkit-transform: translateY(-12px);
    transform: translateY(-12px);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
  }
  82% {
    -webkit-transform: translateY(-6px);
    transform: translateY(-6px);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
  }
  93% {
    -webkit-transform: translateY(-4px);
    transform: translateY(-4px);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
  }
  25%, 55%, 75%, 87% {
    -webkit-transform: translateY(0px);
    transform: translateY(0px);
    -webkit-animation-timing-function: ease-out;
    animation-timing-function: ease-out;
  }
  100% {
    -webkit-transform: translateY(0px);
    transform: translateY(0px);
    -webkit-animation-timing-function: ease-out;
    animation-timing-function: ease-out;
    opacity: 1;
  }
}
@keyframes bounce-top {
  0% {
    -webkit-transform: translateY(-45px);
    transform: translateY(-45px);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
    opacity: 1;
  }
  24% {
    opacity: 1;
  }
  40% {
    -webkit-transform: translateY(-24px);
    transform: translateY(-24px);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
  }
  65% {
    -webkit-transform: translateY(-12px);
    transform: translateY(-12px);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
  }
  82% {
    -webkit-transform: translateY(-6px);
    transform: translateY(-6px);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
  }
  93% {
    -webkit-transform: translateY(-4px);
    transform: translateY(-4px);
    -webkit-animation-timing-function: ease-in;
    animation-timing-function: ease-in;
  }
  25%, 55%, 75%, 87% {
    -webkit-transform: translateY(0px);
    transform: translateY(0px);
    -webkit-animation-timing-function: ease-out;
    animation-timing-function: ease-out;
  }
  100% {
    -webkit-transform: translateY(0px);
    transform: translateY(0px);
    -webkit-animation-timing-function: ease-out;
    animation-timing-function: ease-out;
    opacity: 1;
  }
}
.tabs button.actual {
  background-image: linear-gradient(to right, #00ffff, #ff00c3);
  animation: bounce-top 0.9s forwards;
  color: #FFFFF3;
  pointer-events: none;
}
.tabs button:active {
  background-image: linear-gradient(to right, #00ffff, #ff00c3);
  animation: gradient-animation 1s infinite;
}

.paginacion {
  padding-top: 4rem;
  gap: 1rem;
}
@media (min-width: 480px) {
  .paginacion {
    display: flex;
    justify-content: space-between;
  }
}
.paginacion button {
  margin: 0;
  margin-bottom: 1rem;
}

.ocultar {
  visibility: hidden;
}