@use 'variables' as v;

h1, h2, h3 {
    font-family: v.$fuente_principal;
    margin: 0 0 calc(v.$separacion / 2) 0 ;
    font-weight: 900;
    color: v.$blanco;
    text-align: center;
}

h1 {
    font-size: 4rem;
}
h2 {
    font-size: 3.6rem;
}
h3 {
    font-size: 3rem;
    text-align: center;
}
p {
    color: v.$blanco;
    font-size: 2rem;
}

.nombre-pagina {
    text-align: center;
    margin-bottom: 5rem;
    margin-top: 2rem;
    margin-right: 0rem; 
    margin-left: 0rem;
    font-weight: v.$bold;
}

.descripcion-pagina {
    text-align: center;
}